.baseFont {
  font-size: 14px;
  height: 22px;
}
.baseTipFont {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  line-height: 22px;
  height: 22px;
}
canvas {
  display: block;
}
.tab-chart-mar-10 {
  margin-left: 10px;
}
.analysis_head {
  padding-left: 20px;
  margin-bottom: 20px;
}
.analysis_head > div:first-child > div {
  padding-top: 20px;
  padding-right: 20px;
}
.analysis_head .analysis_card_title {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  line-height: 22px;
  height: 22px;
}
.analysis_head .analysis_card_content {
  height: 38px;
  margin-top: 4px;
  margin-bottom: 0;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  font-size: 30px;
  line-height: 38px;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
.analysis_head .analysis_card_chart {
  height: 46px;
  line-height: 46px;
}
.analysis_head .analysis_card_foot {
  font-size: 14px;
  height: 22px;
  border-top: 1px solid #e8e8e8;
  line-height: 40px;
}
.analysis_tab {
  margin-left: 20px;
  margin-right: 20px;
  background-color: white;
}
.analysis_tab .ant-card-body {
  padding: 0;
}
.analysis_tab .ant-card-body .ant-tabs-nav-container {
  padding: 10px 0 0 20px;
  font-size: 16px;
}
.analysis_tab .tab-extra-wrap {
  display: inline-block;
  cursor: pointer;
}
.analysis_tab .tab-extra-wrap span {
  margin-right: 3px;
}
.tab-chart-rank {
  list-style: none;
  font-size: 14px;
}
.tab-chart-rank li {
  margin: 15px 0;
}
.tab-chart-rank li span:first-child {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-top: 1.5px;
  margin-right: 16px;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  background-color: #f5f5f5;
  border-radius: 20px;
}
.tab-chart-rank li span:first-child.active {
  color: #fff;
  background-color: #314659;
}
.tab-chart-rank li span:last-child {
  float: right;
  width: 80px;
}
.analysis-body {
  margin: 0 20px;
}
.analysis-body > div > div:first-child {
  margin-top: 20px;
}
.analysis-body > div > div:last-child {
  margin: 20px 0;
}
.analysis_foot {
  margin: 0 20px;
}
.tab-chart-mar-50 {
  margin-left: 38px;
}
